'use client';

import { useEffect, useState } from 'react';
import { supabase } from '@/lib/supabase';
import { Cycle, Event, Prediction } from '@/types';
import StatusCard from '@/components/ui/StatusCard';
import PredictionTable from '@/components/ui/PredictionTable';
import { Badge } from '@/components/ui/Badge';
import { Loader2 } from 'lucide-react';

export default function Dashboard() {
  const [loading, setLoading] = useState(true);
  const [activeCycle, setActiveCycle] = useState<Cycle | null>(null);
  const [activeEvent, setActiveEvent] = useState<Event | null>(null);
  const [predictions, setPredictions] = useState<Prediction[]>([]);
  const [user, setUser] = useState<any>(null);

  useEffect(() => {
    async function fetchData() {
      // 1. Get User
      const { data: { user } } = await supabase.auth.getUser();
      setUser(user);

      // 2. Get Active Cycle
      const { data: cycle } = await supabase
        .from('cycles')
        .select('*')
        .eq('status', 'active')
        .single();
      
      if (cycle) {
        setActiveCycle(cycle);

        // 3. Get Latest Event for this Cycle
        const { data: event } = await supabase
          .from('events')
          .select('*')
          .eq('cycle_id', cycle.id)
          .order('created_at', { ascending: false })
          .limit(1)
          .single();

        if (event) {
          setActiveEvent(event);

          // 4. Get Predictions
          const { data: preds } = await supabase
            .from('predictions')
            .select('*')
            .eq('event_id', event.id)
            .order('match_id', { ascending: true });
          
          setPredictions(preds || []);
        }
      }
      setLoading(false);
    }

    fetchData();
  }, []);

  if (loading) {
    return (
      <div className="flex h-[50vh] items-center justify-center">
        <Loader2 className="w-8 h-8 animate-spin text-cyan-500" />
      </div>
    );
  }

  if (!activeCycle || !activeEvent) {
    return (
      <div className="text-center py-20">
        <h2 className="text-2xl font-bold text-gray-500">No Active Cycles Detected.</h2>
        <p className="text-gray-600">The algorithm is sleeping. Check back later.</p>
      </div>
    );
  }

  return (
    <div className="space-y-8 px-4">
      {/* Header Section */}
      <div className="flex flex-col md:flex-row md:items-center justify-between gap-4">
        <div>
          <h1 className="text-3xl font-extrabold text-white tracking-tight">
            {activeCycle.name}
          </h1>
          <div className="flex items-center space-x-2 mt-2">
            <Badge variant="neon">{activeCycle.type} Cycle</Badge>
            <span className="text-gray-500 text-sm">Target: {activeCycle.target_goal}</span>
          </div>
        </div>
        
        {/* Timer Card */}
        <div className="w-full md:w-auto min-w-[300px]">
          <StatusCard deadline={activeEvent.deadline} status={activeCycle.status} />
        </div>
      </div>

      {/* Main Content Area */}
      <div className="bg-gray-900/30 rounded-2xl border border-gray-800 p-1">
        <div className="p-4 border-b border-gray-800 mb-4">
          <h2 className="text-xl font-bold text-white flex items-center">
            {activeEvent.event_name} <span className="text-gray-500 text-sm ml-2 font-normal">({activeEvent.platform})</span>
          </h2>
          <p className="text-sm text-gray-400 mt-1">
            Dual-Strategy Intelligence Matrix. 
            {!user && <span className="text-cyan-400 ml-1">Login to unblur full insights.</span>}
          </p>
        </div>

        <PredictionTable 
          predictions={predictions} 
          isSubscribed={!!user} 
        />
      </div>
    </div>
  );
}